import React from 'react';
import { List } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { Page, PageTitle } from '../Login/styles';

const { Item } = List;

const Setting: React.FC = () => {
  const history = useHistory();
  const handleClick = (type: string): void => {
    switch (type) {
      case 'safe':
        history.push('/safe');
        break;
      case 'feedback':
        history.push('/feedback');
        break;
      default:
    }
  };
  return (
    <Page>
      <PageTitle>通用设置</PageTitle>
      <List>
        <Item arrow="horizontal" onClick={(): void => handleClick('safe')}>
          账号与安全
        </Item>
        <Item arrow="horizontal">菓子题库介绍</Item>
        <Item arrow="horizontal">隐私政策</Item>
        <Item arrow="horizontal" onClick={(): void => handleClick('feedback')}>
          意见反馈
        </Item>
      </List>
    </Page>
  );
};

export default Setting;
